@import "../../assets/styles/global.scss";

.NewOrder {
  position: relative;
  top: -10px;
  padding-right: 20px;
  padding-left: 50px;
  //overflow: hidden;
  @media (max-width: $md) {
    padding-right: 10px;
    padding-left: 10px;
  }
  .breadcrumb {
    margin-left: -0.7rem;
  }

  .userpanel {
    padding: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    .profileIconRound {
      background-color: #fabcc4;
      font-size: 0.625rem;
      width: 30px;
      height: 30px;
      box-shadow: 0px 3px 6px #00000029;
      border-radius: 50%;
      justify-content: center;
      align-items: center;
      margin-right: 11px;
      display: flex;
    }
  }

  .formContainer {
    border-radius: 0.313rem;
    box-shadow: 0px 3px 10px #e4e4e4 !important;
  }

  .form-group,
  .input-group {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .custom-dropdown .btn-custom-dropdown {
      font-size: 14px;
      align-items: center;
      padding-left: 0;
      @media (min-width: $xl) {
        font-size: 14px;
      }
      @media (max-width: $lg) {
        font-size: 12px;
      }
    }

    label {
      // color: #707070;
      text-align: left;
      font-size: 15px;
      margin-bottom: 0px;
    }
    .date {
      border: none;
      background: none;
    }
    .form-control {
      border: none;
      background: transparent;
      border-bottom: 2px solid #d6d6d6;
      border-radius: 0;
      color: $black;
      flex-basis: 55%;
      flex-grow: inherit;
      font-size: 14px;
    }

    @media (min-width: $xl) {
      label {
        font-size: 14px;
        margin-bottom: 0px;
      }

      .form-control {
        border: none;
        background: transparent;
        border-bottom: 2px solid #d6d6d6;
        border-radius: 0;
        color: $black;
        flex-basis: 65%;
        flex-grow: inherit;
        font-size: 14px;
      }
    }
  }
  .line {
    border-bottom: 2px solid #d6d6d6;
    padding: 0;
    color: $black;
    flex-basis: 65%;

    font-size: 14px;
    width: 400px;
  }
  .name {
    color: #707070;
    font-weight: 400;
    font-size: 14px;
    padding: 0;
  }
  .org {
    position: relative;
    top: 10px;
  }
  .orgV {
    position: relative;
    top: 10px;
    right: 150px;
    color: #333333;
    @media screen and (min-width:"1166px"){
      right: 280px;
    }
    @media screen and (min-width:"1266px"){
      right: 340px;
    }
    @media screen and (min-width:"1366px"){
      right: 340px;
    }
    @media screen and (min-width:"1466px"){
      right: 400px;
    }
    @media screen and (min-width:"1566px"){
      right: 460px;
    }
  }

  .input-group {
    position: relative;
    margin-bottom: 1rem;

    .input-group-append {
      position: absolute;
      right: 0;
      cursor: pointer;
    }
  }

  .low {
    padding-bottom: 5px;
    border-radius: 10px;
  }
  .headsup1 {
    margin-top: -1rem;
    padding-bottom: 0rem;
    padding-left: 16px;
  }
  .headsup {
    padding-top: 1.25rem;
    padding-bottom: 0.625rem;
  }
  .total {
    color: $OBBlue;
    font: 25px/13px $roboto;
  }
  .value {
    color: $black;
    font: 25px/13px $roboto;
  }

  hr {
    border-width: 2px;
    border-color: #d3d3d3;
  }

  .btn-outline-info {
    border-radius: 10px;
  }

  .tooltip {
    position: relative;
  }
  .tooltip::before {
    background-color: white;
    border: 1px solid #888;
    border-radius: 2px;
    color: #444;
    content: attr(data-title);
    display: none;
    font-family: sans-serif;
    font-size: 14px;
    padding: 2px 5px;
    position: absolute;
    top: 20px;
    left: 5px;
    z-index: 1;
  }
  .tooltip:hover::before {
    display: block;
  }

  .table {
    .overflow {
      height: auto;
      max-height: 400px;
    }
  }
}

.shipmentpopup {
  .data {
    font: 16px/27px $roboto;
    color: #707070;
  }

  .error {
    font: 22px/36px $roboto;
    color: #707070;
  }

  .alert {
    font: 25px/0.1px $roboto;
    color: #000000;
  }
}
.select-placeholder-text {
  color: #a8a8a8;
}
.sidename {
  font-size: 16px;
}
.text-danger1 {
  color: rgb(216, 9, 9);
  font-size: 16px;
  padding: 0.75rem 2rem;
}
