.client-card > img {
  width: 8rem;
  object-fit: contain;
  cursor: pointer;
}

.client-container {
  padding-top: 2.5rem;
  padding-bottom: 1.5rem;
  display: flex;
  align-items: center;
  grid-gap: 2.75rem;
  gap: 2.75rem;
  overflow-x: auto;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.client-container::-webkit-scrollbar {
  display: none;
}

.section-headers > h1 {
  position: relative;
}
.section-headers > h1::after {
  content: "";
  position: absolute;
  inset: auto auto -0.6rem 0;
  width: 2rem;
  height: 3px;
  background-color: brown;
}

.contact-form {
  width: 100%;
  max-width: 36rem;
  display: block;
  margin: 0 auto;
  padding: 2rem 1.5rem;
  border-radius: 0.8rem;
  display: flex;
  flex-direction: column;
  grid-gap: 2rem;
  gap: 2rem;
  position: relative;
}

.form-headers-popup {
  display: flex;
  flex-direction: column;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.popup-close-icon {
  position: absolute;
  top: 2rem;
  right: 1.5rem;
  width: 35px;
  height: 35px;
  border-radius: 0.3rem;
  display: grid;
  place-content: center;
  background-color: var(--white);
  color: var(--grey-light) !important;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
.popup-close-icon:hover {
  background-color: #f5f8fa;
  color: var(--brand-link) !important;
}

.feature-two-column-grid-container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 2.5rem;
  gap: 2.5rem;
}
.feature-left-container {
  background-color: var(--brand-primary);
  border-radius: 0.8rem;
  padding: 2rem;
  position: relative;
}

.feature-right-container {
  display: flex;
  flex-direction: column;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.feature-heading-groups {
  display: flex;
  flex-direction: column;
  grid-gap: 2.5rem;
  gap: 2.5rem;
  z-index: 1000 !important;
}
.bar-space-image {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
}

@media screen and (max-width: 992px) {
  .feature-two-column-grid-container {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 2.5rem;
    gap: 2.5rem;
  }
  .bar-space-image {
    display: none;
    position: absolute;
    left: 0;
    bottom: 0;
  }
  .feature-left-container {
    /* background: var(--brand-primary); */
    background: linear-gradient(
        99.16deg,
        rgba(0, 98, 255, 0.5) 34.61%,
        rgba(40, 122, 255, 0.5) 73.1%
      ),
      url(/static/media/bars.db5a4d8d.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 0.8rem;
    padding: 2rem;
    position: relative;
  }
}

@media screen and (max-width: 586px) {
  .feature-left-container {
    padding: 1.5rem;
  }
  .feature-two-column-grid-container {
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }
}

.newscard-logo-image {
  width: 8rem;
  object-fit: contain;
}

.newscard-section {
  display: grid;
  grid-template-columns: 8rem 1fr;
  grid-gap: 2.5rem;
