@import "../../assets/styles/global.scss";

.spacingbutton {
  padding-right: 0px;
  padding-left: 90px;
}
.text-danger-EL {
  color: rgb(212, 2, 2);
  font-size: 12px;
}
.form-control2 {
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
    color: #707070;
  }
}
.line1 {
  border-bottom: 2px solid #d6d6d6;
  padding: 0;
  color: $black;
  flex-basis: 47%;
  font-size: 14px;
}
.name {
  color: #707070;
  font-weight: 400;
  font-size: 14px;
  padding: 0;
}
.editlocP {
  font-size: 14px;
  width: 65%;
  padding: 6px 0px;
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid #d1cbcb;
  //-webkit-transition: 0.1s;
  //transition: 0.1s;
  outline: none;
  float: right;
}
.editlocP:focus {
  border-radius: 6px;
  border: 2px solid #2196f3;
}
.MuiInputBase-input {
  margin-left: 0px !important;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
